import React, { useState } from 'react';
import { Layout, Input, Button, Select } from 'antd';

const { Header, Content } = Layout;
const { Option } = Select;

function App() {
  const [mode, setMode] = useState('');
  const [inputX, setInputX] = useState('');
  const [inputY, setInputY] = useState('');
  const [inputZ, setInputZ] = useState('');
  const [result, setResult] = useState('');

  const handleModeChange = (newMode) => {
    setMode(newMode);
    setInputX('');
    setInputY('');
    setInputZ('');
    setResult('');
  };

  const convertCoordinates = () => {
    if (mode === '1') {
      const x2 = parseInt(inputX) * 8;
      const y2 = parseInt(inputY) * 8;
      const z2 = parseInt(inputZ) * 8;
      setResult(
        <p style={{ fontSize: '24px' }}>: {x2} {y2} {z2}</p>
      );
    } else if (mode === '2') {
      const x2 = parseInt(inputX) / 8;
      const y2 = parseInt(inputY) / 8;
      const z2 = parseInt(inputZ) / 8;
      setResult(
        <p style={{ fontSize: '24px' }}>: {x2} {y2} {z2}</p>
      );
    }
  };

  return (
    <Layout>
      <Header style={{ textAlign: 'left', padding: '50px' }}>
      <h1 style={{ color: 'white', fontSize: '36px', position: 'relative', top: '-55px'  }}>Minecraft</h1>
      </Header>
      <Content style={{ padding: '16px' }}>
        <div>
          <h2>:</h2>
          <Select
            style={{ width: 200, marginBottom: 16 }}
            placeholder=""
            onChange={handleModeChange}
            value={mode}
          >
            <Option value="1"></Option>
            <Option value="2"></Option>
          </Select>
          
          {(mode === '1' || mode === '2') && (
            <div>
              <Input
                placeholder={mode === '1' ? 'X' : 'X'}
                onChange={(e) => setInputX(e.target.value)}
                value={inputX}
              />
              <Input
                placeholder={mode === '1' ? 'Y' : 'Y'}
                onChange={(e) => setInputY(e.target.value)}
                value={inputY}
              />
              <Input
                placeholder={mode === '1' ? 'Z' : 'Z'}
                onChange={(e) => setInputZ(e.target.value)}
                value={inputZ}
              />
              <Button type="primary" onClick={convertCoordinates} style={{ marginTop: 16 }}>
                
              </Button>
              {result}
            </div>
          )}
        </div>
      </Content>
    </Layout>
  );
}

export default App;
